import { Component, OnInit, OnDestroy, Inject } from '@angular/core';
import { trigger, style, animate, transition } from '@angular/animations';
import { BreakpointObserver, Breakpoints, BreakpointState } from '@angular/cdk/layout';
import { MatDialog, MAT_DIALOG_DATA } from '@angular/material/dialog';
import { DialogSignInComponent } from './signIn/sign-in.component';


@Component({
  selector: 'app-root',
  animations: [
    trigger(
      'enterAnimation', [
      transition(':enter', [
        style({ width: '0px' }),
        animate('200ms', style({ width: '250px' }))
      ]),
      transition(':leave', [
        style({ width: '250px' }),
        animate('200ms', style({ width: '0px' }))
      ])
    ]
    )
  ],
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})

export class AppComponent implements OnInit, OnDestroy {
  search = '';
  title = 'شبكني';
  showSearch = false;
  showSideMenu = false;
  photoIndex = 0;
  photos = [
    '../assets/header-background-puzzle.webp',
    '../assets/header-background-blue.png',
  ];
  photosIndexInterval = null;
  breakpoint: string;
  aboutContainerColumns: number;
  aboutTextColumns: number;
  aboutLogoColumns: number;
  aboutRowHeight: string;

  constructor(private breakpointObserver: BreakpointObserver, public dialog: MatDialog) { }

  ngOnDestroy(): void {
    clearInterval(this.photosIndexInterval);
  }

  ngOnInit(): void {
    this.photosIndexInterval = setInterval(() => {
      this.photoIndex++;
    }, 20000);

    // this.showSideMenu = this.breakpointObserver.isMatched('(max-width: 960px)');

    const layoutChanges = this.breakpointObserver.observe([
      Breakpoints.XSmall,
      Breakpoints.Small,
      Breakpoints.Medium,
      Breakpoints.Large,
      Breakpoints.XLarge
    ]).subscribe((state: BreakpointState) => {
      this.updateView(state);
    });
  }

  updateView(state: BreakpointState) {
    if (state.breakpoints[Breakpoints.XSmall]) {
      this.showSideMenu = true;
      this.breakpoint = 'xs';
      this.aboutContainerColumns = 1;
      this.aboutLogoColumns = 0;
      this.aboutTextColumns = 1;
      this.aboutRowHeight = '150px';
    }
    if (state.breakpoints[Breakpoints.Small]) {
      this.showSideMenu = true;
      this.breakpoint = 'sm';
      this.aboutContainerColumns = 5;
      this.aboutLogoColumns = 1;
      this.aboutTextColumns = 3;
      this.aboutRowHeight = '150px';
    }
    if (state.breakpoints[Breakpoints.Medium]) {
      this.showSideMenu = false;
      this.breakpoint = 'm';
      this.aboutContainerColumns = 5;
      this.aboutLogoColumns = 1;
      this.aboutTextColumns = 3;
      this.aboutRowHeight = '100px';
    }
    if (state.breakpoints[Breakpoints.Large]) {
      this.showSideMenu = false;
      this.breakpoint = 'l';
      this.aboutContainerColumns = 5;
      this.aboutLogoColumns = 1;
      this.aboutTextColumns = 3;
      this.aboutRowHeight = '100px';
    }
    if (state.breakpoints[Breakpoints.XLarge]) {
      this.showSideMenu = false;
      this.breakpoint = 'xl';
      this.aboutContainerColumns = 4;
      this.aboutLogoColumns = 1;
      this.aboutTextColumns = 2;
      this.aboutRowHeight = '100px';
    }
  }

  openSignInDialog(): void {
    const dialogRef = this.dialog.open(DialogSignInComponent, {
      width: '350px'
    });

    // dialogRef.afterClosed().subscribe(result => {
    //   console.log('The dialog was closed');
    // });
  }
}


